body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ag-theme-balham .ag-filter label {
    padding-left: 30px;
}
button#search {
    position: relative;
    top: 25px;
    left: 15px;
}

div#searchResult {
    overflow-y: auto;
    max-height: 410px;
}

div#searchResult ul.collection {
    border: 0;
}

div#searchResult li.collection-item {
    border: 1px black !important;
}

div#video {
    padding-top: 150px;
    text-align: center;
    border: solid 1px black; 
    height: 400px;
}

@media screen and (min-width: 902px) {
    
    .entityLogo {
        width: 55%;
        height: auto;
    }

}

@media screen and (max-width: 902px) {

    .entityLogo {
        width: 100%;
        height: auto;
    }

}
div.menuOptions { 
    text-align: center; 
    vertical-align: center; 
    height: 150px;
    background-color: #26A69A; 
    margin: 5px;
    padding: 0 !important;
}

div.menuOptions :hover {
    background-color: #2BBBAD;
}

a.menuButton {
    display: block;
    width: 100%;
    height: 100%;
    font-size: bold;
    color: black;
}
